@import "_variables";

body {
  background-color: $grey;
  width: 100%;
  overflow-x: hidden;
}

.container-fluid {padding: 0;}


main {
      overflow-x: hidden;
      margin: $header-height $margin-width 0 $margin-width;
}

a {
  color: black;
  text-decoration: none;
}
a:hover {
  color: black;
  text-decoration: underline;
}

@media (max-width: 991.98px) {
  main {
        overflow-x: hidden;
        margin: $header-height-mobile $margin-width-mobile 0 $margin-width-mobile;
  }

}


.hidden {
  display: none;
}

body::-webkit-scrollbar,  {
  display: none;
}
