@import "global";
@import "_variables";


.header {
  height: $header-height;
  position: fixed;
  top: 0;
  left: 0;
  background-color: $grey;
  z-index: 100;
  padding: 0 $margin-width 0 $margin-width;

.navigator {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

img {
  height: 100%;
  padding-bottom: 25px;
  padding-top: $margin-width;
}

#header-about {
  padding-right: 2rem;
}

}

@media (max-width: 991.98px) {


  .header {
    position: fixed;
    background-color: transparent;
    z-index: 100;
    padding: 0 $margin-width-mobile 0 $margin-width-mobile;

  .navigator {
    color: black;
    text-decoration: none;
    padding: 0.5rem;
    border-radius: $margin-width-mobile;
    background-color: rgba(255, 255, 255, 0.8);
  }

  img {
    height: 2.5rem;
    padding: 0.5rem;
    border-radius: $margin-width-mobile;

    background-color: rgba(255, 255, 255, 0.8);
  }

  #header-about {
    padding-right: 0.5rem;
  }

  .d-block {
    left: 0; right: 0;
    text-align: center;
    position: absolute;
  }

  .d-flex {
    width: 100%;
  }
}
 }
